import { NextResponse } from 'next/server'
import type { NextRequest } from 'next/server'
import { getToken } from 'next-auth/jwt'

export async function middleware(request: NextRequest) {
  const token = await getToken({ req: request })
  const pathname = request.nextUrl.pathname

  // Protected routes that require authentication
  if (pathname.startsWith('/dashboard')) {
    if (!token) {
      return NextResponse.redirect(new URL('/admin', request.url))
    }

    // Handle admin routes
    if (pathname.startsWith('/dashboard/users') && token?.role !== 'ADMIN') {
      return NextResponse.redirect(new URL('/dashboard', request.url))
    }

    return NextResponse.next()
  }

  // Handle login page
  if (pathname === '/admin') {
    if (token) {
      return NextResponse.redirect(new URL('/dashboard', request.url))
    }
    return NextResponse.next()
  }

  // Check whitelist status for both form and verify pages
  if (pathname === '/' || pathname === '/verify') {
    try {
      // Check if whitelist is enabled via API using localhost
      const settingsResponse = await fetch(
        'http://localhost:3000/api/whitelist/check-enabled',
        { cache: 'no-store' }  // Ensure we get fresh data
      )
      
      if (!settingsResponse.ok) {
        throw new Error('Failed to check whitelist status')
      }
      
      const settingsData = await settingsResponse.json()
      
      // If whitelist is disabled and we're on /verify, redirect to form
      if (pathname === '/verify' && (!settingsData.success || !settingsData.enabled)) {
        return NextResponse.redirect(new URL('/', request.url))
      }
      
      // Handle form access (homepage) with whitelist enabled
      if (pathname === '/' && settingsData.success && settingsData.enabled) {
        const username = request.nextUrl.searchParams.get('username')
        const platform = request.nextUrl.searchParams.get('platform')
        
        if (!username || !platform) {
          return NextResponse.redirect(new URL('/verify', request.url))
        }

        try {
          // Check if user is whitelisted via API using localhost
          const whitelistResponse = await fetch(
            `http://localhost:3000/api/whitelist/check?username=${encodeURIComponent(username)}&platform=${encodeURIComponent(platform)}`,
            { cache: 'no-store' }  // Ensure we get fresh data
          )
          
          if (!whitelistResponse.ok) {
            throw new Error('Failed to check whitelist status')
          }
          
          const whitelistData = await whitelistResponse.json()

          if (!whitelistData.success || !whitelistData.isWhitelisted) {
            return NextResponse.redirect(new URL('/verify?error=not_whitelisted', request.url))
          }
        } catch (error) {
          console.error('Error checking whitelist:', error)
          return NextResponse.redirect(new URL('/verify?error=server_error', request.url))
        }
      }
    } catch (error) {
      console.error('Error checking whitelist setting:', error)
      return NextResponse.redirect(new URL('/verify?error=server_error', request.url))
    }
  }

  return NextResponse.next()
}

// Protect these routes
export const config = {
  matcher: [
    '/dashboard/:path*',
    '/admin',
    '/',
    '/verify'
  ]
} 